import React, { Component } from "react";
import { ImFilesEmpty } from "react-icons/im";
import { connect } from "react-redux";
import {
  FC_GetMyTransfers,
  FT_GetMyTransfer,
} from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import Container from "../../../components/ContainerComponents/Container";
import DashboardContainer from "../../../components/DashboardContainer/DashboardContainer";
import Heading from "../../../components/Heading/Heading";
import TextInput from "../../../components/Inputs/TextInput";
import Loading from "../../../components/Loading/Loading";
import TableLoading from "../../../components/TableLoading/TableLoading";
import { StoreState } from "../../../reducers";
import { RequestedTransfer } from "../../../types/TransferTypes";
import { DATE, isEmptyOrSpaces, search } from "../../../utils/functions";
import RequestForm from "./RequestForm";
import RequestView from "./RequestView";

const FC_TransferRequestedDate = (
  requested: string | null,
  outGoing: string | null
): JSX.Element => {
  if (requested === "PENDING")
    return (
      <span className="font-bold rounded py-2 px-4 text-md bg-accent-800 text-black group-hover:bg-opacity-90">
        Transfer in progress
      </span>
    );
  if (requested === "REJECTED")
    return (
      <span className="font-bold rounded py-2 px-4 text-md bg-red-700 text-black group-hover:bg-opacity-90">
        Transfer rejected
      </span>
    );
  if (requested === "APPROVED" && outGoing === null)
    return (
      <span className="font-bold rounded py-2 px-4 text-md bg-green-700 text-black group-hover:bg-opacity-90">
        Apply for leave
      </span>
    );
  if (requested === "APPROVED" && outGoing === "APPROVED")
    return (
      <span className="font-bold rounded py-2 px-4 text-md bg-green-600 text-black group-hover:bg-opacity-90">
        Transfer Completed
      </span>
    );
  if (requested === "APPROVED" && outGoing === "REJECTED")
    return (
      <span className="font-bold rounded py-2 px-4 text-md bg-red-700 text-black group-hover:bg-opacity-90">
        Leave rejected
      </span>
    );

  return <></>;
};

// status Handler
const FC_TransferRequestedStatus = (
  requested: string | null,
  outGoing: string | null
): JSX.Element => {
  if (requested === "PENDING")
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-accent-800 text-black group-hover:bg-opacity-90">
        Transfer in progress
      </span>
    );
  if (requested === "REJECTED")
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-red-700 text-red-50 group-hover:bg-opacity-90">
        Transfer rejected
      </span>
    );
  if (requested === "APPROVED" && outGoing === null)
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-green-700 text-green-50 group-hover:bg-opacity-90">
        Apply for leave
      </span>
    );
  if (requested === "APPROVED" && outGoing === "PENDING")
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-accent-800 text-black group-hover:bg-opacity-90">
        Leave in progress
      </span>
    );
  if (requested === "APPROVED" && outGoing === "APPROVED")
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-green-600 text-green-50 group-hover:bg-opacity-90">
        Transfer Completed
      </span>
    );
  if (requested === "APPROVED" && outGoing === "REJECTED")
    return (
      <span className="font-bold rounded-full py-1 px-2 text-md bg-red-700 text-red-50 group-hover:bg-opacity-90">
        Leave rejected
      </span>
    );

  return <></>;
};

type RequestTransferProps = {
  myTransfers: RequestedTransfer[] | null;
  FC_GetMyTransfers: FT_GetMyTransfer;
};

type RequestTransferState = {
  loading: boolean;
  loadingRequest: boolean;
  error: {
    target: "main-error" | "main-success";
    msg: string;
  } | null;
  inputSearch: string;
  transferId: string;
  isRequestFormOn: boolean;
};

class index extends Component<RequestTransferProps, RequestTransferState> {
  constructor(props: RequestTransferProps) {
    super(props);

    this.state = {
      loading: true,
      loadingRequest: false,
      error: null,
      inputSearch: "",
      transferId: "",
      isRequestFormOn: false,
    };
  }

  setIsRequestFormOn = (value: boolean) => {
    this.setState({ isRequestFormOn: value });
  };

  setTransferId = (value: string) => {
    this.setState({ transferId: value });
  };

  componentDidMount = () => {
    this.props.FC_GetMyTransfers(
      (status: boolean, data: RequestedTransfer[] | null, msg: string) => {
        this.setState({ loading: status });

        if (!status && data) this.setState({ loading: status });
        else if (!status && !isEmptyOrSpaces(msg))
          this.setState({
            error: { target: "main-error", msg: msg },
          });
      }
    );
  };

  isSchoolRequestedAllowed = (): boolean => {
    const result =
      this.props.myTransfers &&
      this.props.myTransfers.find(
        (item) => item.outgoing_status !== "APPROVED"
      );

    return result ? false : true;
  };

  render() {
    if (this.state.loading)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (this.state.isRequestFormOn)
      return <RequestForm setIsRequestFormOn={this.setIsRequestFormOn} />;

    if (
      this.props.myTransfers &&
      this.props.myTransfers.find(
        (item) => item.teacher_transfer_id === this.state.transferId
      )
    )
      return (
        <RequestView
          selectedTransfer={
            this.props.myTransfers.find(
              (item) => item.teacher_transfer_id === this.state.transferId
            )!
          }
          setTransferId={this.setTransferId}
        />
      );

    return (
      <>
        {this.state.error && this.state.error.target === "main-success" && (
          <Alert theme="success">{this.state.error.msg}</Alert>
        )}
        {this.state.error && this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}
        <DashboardContainer>
          {/* TITLE ======= */}
          <section className="flex flex-col sm:flex-row justify-between">
            <div className="px-3">
              <Heading size="lg">Transfer request(s)</Heading>
            </div>

            {this.isSchoolRequestedAllowed() && (
              <Button
                theme="primary-light"
                onClick={() => this.setIsRequestFormOn(true)}
              >
                Request Transfer
              </Button>
            )}
          </section>

          {/* BODY=================== */}
          <div className="border border-t mt-2 mb-1 flex items-center flex-col-reverse sm:flex-row pl-3 pr-2 rounded">
            <div className="w-full sm:flex-1 pr-2">
              <TextInput
                error=""
                label="Search"
                name=""
                type="search"
                onChange={(e) => this.setState({ inputSearch: e.target.value })}
                placeholder="Search by name, reason, date, status"
                value={this.state.inputSearch}
                size="sm"
                disabled={
                  this.props.myTransfers === null ||
                  this.props.myTransfers.length === 0
                }
              />
            </div>
            {/* <div className="w-full sm:w-auto">
            <InputSelect
              data={[
                { title: "All", value: "all" },
                { title: "Pending", value: TerminationStatusEnum.PENDING },
                {
                  title: "Terminated",
                  value: TerminationStatusEnum.TERMINATED,
                },
                { title: "Rejected", value: TerminationStatusEnum.REJECTED },
              ]}
              error=""
              name="select_status"
              onChange={(e) =>
                this.setState({
                  thisStatus: e.target.value as TerminationStatusEnum | "all",
                })
              }
              label="Status"
              placeholder="Select status"
              value={this.state.thisStatus}
              size="sm"
            />
          </div> */}
            {/* {this.props.terminations && (
            <div className="p-2 px-3 ml-3 bg-accent-800 bg-opacity-30 rounded">
              <label className="">Results:</label>
              <h4 className="text-2xl -mt-1 font-bold">
                {
                  this.filterTerminations(
                    this.props.terminations,
                    this.state.thisStatus
                  ).length
                }
              </h4>
            </div>
          )} */}
          </div>
          <div className="my-3 w-full overflow-x-auto">
            <table className="w-full text-left table-auto">
              <thead>
                <tr>
                  <th className="p-2">#</th>
                  <th className="p-2">Requested school</th>
                  <th className="p-2">Reason</th>
                  <th className="p-2">Date</th>
                  <th className="p-2">Status</th>
                  <th className="p-2"></th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading ? (
                  <TableLoading cols={6} />
                ) : this.props.myTransfers === null ||
                  this.props.myTransfers.length <= 0 ? (
                  <tr>
                    <td colSpan={6} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found!
                        </span>
                      </div>
                    </td>
                  </tr>
                ) : search(this.props.myTransfers, this.state.inputSearch)
                    .length === 0 ? (
                  <tr>
                    <td colSpan={6} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found! Please try to search again
                        </span>
                      </div>
                    </td>
                  </tr>
                ) : (
                  search(this.props.myTransfers, this.state.inputSearch).map(
                    (transfer: RequestedTransfer, i: number) => {
                      return (
                        <tr
                          className="group border-b border-t rounded hover:bg-accent-800 hover:bg-opacity-20 cursor-pointer"
                          onClick={() =>
                            this.setState({
                              transferId: transfer.teacher_transfer_id,
                            })
                          }
                          key={i + transfer.teacher_transfer_id}
                        >
                          <td className="p-2">{i + 1}</td>
                          <td className="p-2">
                            {transfer.requested_school_name}
                          </td>
                          <td className="p-2">{transfer.teacher_reason}</td>
                          <td className="p-2">
                            {transfer.outgoing_decision_date
                              ? DATE(transfer.outgoing_decision_date)
                              : transfer.incoming_decision_date
                              ? DATE(transfer.incoming_decision_date)
                              : DATE(transfer.teacher_requested_transfer_date)}
                          </td>
                          <td className="p-2">
                            {FC_TransferRequestedStatus(
                              transfer.requested_status,
                              transfer.outgoing_status
                            )}
                          </td>
                          <td className="p-1 text-center">
                            <Button
                              theme="accent-light"
                              onClick={() =>
                                this.setState({
                                  transferId: transfer.teacher_transfer_id,
                                })
                              }
                            >
                              View
                            </Button>
                          </td>
                        </tr>
                      );
                    }
                  )
                )}
              </tbody>
            </table>
          </div>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  transfer,
}: StoreState): {
  myTransfers: RequestedTransfer[] | null;
} => {
  return { myTransfers: transfer.myTransfers };
};

export const RequestTransfer = connect(mapStateToProps, { FC_GetMyTransfers })(
  index
);
