import axios from "axios";
import React, { Fragment, useState } from "react";
import { AiOutlineCheckCircle, AiOutlineCloseCircle } from "react-icons/ai";
import { ImAttachment } from "react-icons/im";
import { MdOutlinePending } from "react-icons/md";
import { connect } from "react-redux";
import { Auth } from "../../../actions";
import {
  FC_GetMyTransfers,
  FT_GetMyTransfer,
} from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import DashboardContainer from "../../../components/DashboardContainer/DashboardContainer";
import DownloadLink from "../../../components/DownloadLink/DownloadLink";
import Heading from "../../../components/Heading/Heading";
import { APP_URL, APP_URL_FILE } from "../../../config/app.config";
import { StoreState } from "../../../reducers";
import { RequestedTransfer, SchoolsType } from "../../../types/TransferTypes";
import { setAxiosToken } from "../../../utils/AxiosToken";
import { DATE, isEmptyOrSpaces } from "../../../utils/functions";

type RequestViewProps = {
  selectedTransfer: RequestedTransfer;
  setTransferId: (value: string) => void;

  //   redux ==========
  auth: Auth;
  FC_GetMyTransfers: FT_GetMyTransfer;
};

const RequestView: React.FC<RequestViewProps> = (props) => {
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [error, setError] = useState<{
    target: "main-error" | "main-success";
    msg: string;
  } | null>(null);
  // on chacking request transfer Approved
  const requesteTransferHandler = (value: string | null) => {
    if (value === null || value === "PENDING") {
      return (
        <span className={`text-accent-800`}>
          <MdOutlinePending size={30} className="fill-current" />
        </span>
      );
    }
    return value === "APPROVED" ? (
      <span className={`text-primary-800`}>
        <AiOutlineCheckCircle size={30} className="fill-current" />
      </span>
    ) : (
      <span className={`text-red-800`}>
        <AiOutlineCloseCircle size={30} className="fill-current" />
      </span>
    );
  };

  // Sending Leaving Request Handler
  const leavingRequestHandler = (value: string) => {
    setAxiosToken();
    axios
      .post(`${APP_URL}/teachertransfers/leaving`, {
        teacher_transfer_id: value,
      })
      .then((response): SchoolsType => response.data)
      .then((data) => {
        console.log("====================================");
        console.log({ leavingRequestHandler: data });
        console.log("====================================");
        props.FC_GetMyTransfers(
          (status: boolean, data: RequestedTransfer[] | null, msg: string) => {
            setLoadingRequest(status);

            if (!status && data) setLoadingRequest(status);
            else if (!status && !isEmptyOrSpaces(msg))
              setError({ target: "main-error", msg: msg });
          }
        );
        // let thisTransfer = { ...this.state.selectedTransfer };
        props.FC_GetMyTransfers(
          (status: boolean, data: RequestedTransfer[] | null, msg: string) => {
            setLoadingRequest(status);

            if (!status && data) setLoadingRequest(status);
            else if (!status && !isEmptyOrSpaces(msg))
              setError({ target: "main-error", msg: msg });
          }
        );
        setError({
          target: "main-success",
          msg: "Leaving Request Send Successfully!",
        });
      })
      .catch((err) => {
        setError({
          target: "main-error",
          msg: "Something went Wrong, Sending Request Failed!",
        });
      });
  };
  return (
    <>
      {error && error.target === "main-success" && (
        <Alert theme="success">{error.msg}</Alert>
      )}
      {error && error.target === "main-error" && (
        <Alert theme="danger">{error.msg}</Alert>
      )}
      <DashboardContainer>
        <div className="py-2 flex items-center">
          <div className="flex-1">
            <Heading>Request Transfer Progress</Heading>
          </div>
          <div className="">
            <Button onClick={() => props.setTransferId("")} theme="default">
              Close
            </Button>
          </div>
        </div>
        <h2 className="opacity-60 -mt-1 font-bold text-accent-900 dark:text-accent-800">
          {props.selectedTransfer.requested_school_name}
        </h2>
        <div className="mt-4">
          <ul>
            {/* Request Transfer Send */}
            <li className="flex text-base text-body-color mb-4 ">
              <div className="flex">
                <div>
                  <span className="text-primary-800">
                    <AiOutlineCheckCircle size={30} className="fill-current" />
                  </span>
                </div>
                <div className="ml-2">
                  <span className="">
                    Requested school:{" "}
                    {props.selectedTransfer.requested_school_name}
                  </span>
                  <br />
                  <span className="">
                    On:{" "}
                    {DATE(
                      props.selectedTransfer.teacher_requested_transfer_date
                    )}
                  </span>{" "}
                  {/* <br />
                            <span className="">Send Transfer Request</span> */}
                </div>
              </div>
            </li>
            {/* When Requested districted Appreved or Rejected */}
            {
              <li className="flex text-base text-body-color mb-4">
                <div className="flex">
                  <div>
                    {requesteTransferHandler(
                      props.selectedTransfer.requested_status
                    )}
                  </div>
                  {props.selectedTransfer.requested_status !== "PENDING" && (
                    <div className="ml-2">
                      <span className="">
                        Approved school:{" "}
                        {props.selectedTransfer.approved_school_name}
                      </span>
                      <br />
                      <span className="">
                        On:{" "}
                        {DATE(props.selectedTransfer.incoming_decision_date)}
                      </span>
                      <br />
                    </div>
                  )}
                </div>
              </li>
            }
            {/* When Send Requested to Leave School */}
            {props.selectedTransfer.requested_status !== "PENDING" &&
              props.selectedTransfer.requested_status !== "REJECTED" &&
              props.selectedTransfer.requested_status !== null && (
                <Fragment>
                  <li className="flex text-base text-body-color mb-4">
                    <div className="flex">
                      {props.selectedTransfer.outgoing_status === null && (
                        <div>
                          {" "}
                          <Button
                            onClick={(e) =>
                              leavingRequestHandler(
                                props.selectedTransfer.teacher_transfer_id
                              )
                            }
                          >
                            Send Leave Request
                          </Button>
                        </div>
                      )}
                      {props.selectedTransfer.outgoing_status !== null && (
                        <Fragment>
                          <div>
                            <span className="text-primary-800">
                              <AiOutlineCheckCircle
                                size={30}
                                className="fill-current"
                              />
                            </span>
                          </div>
                          <div className="ml-2">
                            <span className="">
                              Requested to leave:{" "}
                              {props.auth.school
                                ? props.auth.school.school_name
                                : ""}
                            </span>
                            <br />
                            <span className="">
                              On:{" "}
                              {DATE(
                                props.selectedTransfer
                                  .teacher_requested_transfer_date
                              )}
                            </span>
                          </div>
                        </Fragment>
                      )}
                    </div>
                  </li>
                  {/* When Dde Commirfmed Requested to Leave School */}
                  <li className="flex text-base text-body-color mb-4"></li>
                  {props.selectedTransfer.outgoing_status && (
                    <li className="flex text-base text-body-color mb-4">
                      <div className="flex">
                        <div>
                          {requesteTransferHandler(
                            props.selectedTransfer.outgoing_status
                          )}
                        </div>
                        <div className="ml-2">
                          {props.selectedTransfer.outgoing_status !==
                            "PENDING" && (
                            <>
                              <span className="">
                                Date:{" "}
                                {props.selectedTransfer.outgoing_decision_date}
                              </span>{" "}
                              <br />
                            </>
                          )}
                          <span className="capitalize">
                            Leave Request:{" "}
                            {props.selectedTransfer.outgoing_status}
                          </span>
                          <br />
                          {props.auth.user &&
                            props.selectedTransfer.outgoing_status !==
                              "PENDING" &&
                            props.selectedTransfer.mayor_letter && (
                              <DownloadLink
                                linkFor="uploaded"
                                first_id={props.selectedTransfer.mayor_letter}
                                last_id={""}
                                title={"Transfer letter"}
                              />
                            )}
                          {/* <span className="">Current School</span> */}
                        </div>
                      </div>
                    </li>
                  )}
                </Fragment>
              )}
          </ul>
        </div>
      </DashboardContainer>
    </>
  );
};

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return { auth };
};
export default connect(mapStateToProps, {
  FC_GetMyTransfers,
})(RequestView);
