import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth } from "../../../actions";
import {
  TransferReasonsType,
  FC_GetTransferReasons,
  FT_GetTransferReasons,
  FC_GetSchoolByDistrict,
  FC_UploadFile,
  FC_GetMyTransfers,
  FT_GetMyTransfer,
  FC_GetDistricts,
} from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import DashboardContainer from "../../../components/DashboardContainer/DashboardContainer";
import Heading from "../../../components/Heading/Heading";
import InputFile, { acceptAllEnum } from "../../../components/Inputs/InputFile";
import InputSelect from "../../../components/Inputs/InputSelect";
import TextArea from "../../../components/Inputs/TextArea";
import Loading from "../../../components/Loading/Loading";
import SelectCustom from "../../../components/SelectCustom/SelectCustom";
import { APP_URL } from "../../../config/app.config";
import { StoreState } from "../../../reducers";
import {
  DistrictTypes,
  RequestedTransfer,
  SchoolsType,
} from "../../../types/TransferTypes";
import { setAxiosToken } from "../../../utils/AxiosToken";
import { isEmptyOrSpaces } from "../../../utils/functions";

type RequestFormProps = {
  setIsRequestFormOn: (value: boolean) => void;

  // form redux
  auth: Auth;
  transferReasons: TransferReasonsType[] | null;
  FC_GetTransferReasons: FT_GetTransferReasons;
  FC_GetMyTransfers: FT_GetMyTransfer;
};

type RequestFormState = {
  loading: boolean;
  error: {
    target:
      | "main-error"
      | "main-success"
      | "reason_id"
      | "approved_doc"
      | "school-code"
      | "comment";
    msg: string;
  } | null;
  selectedFile: File | null;
  selectReason: boolean;
  thisReason: TransferReasonsType | null;
  districts: DistrictTypes[] | null;
  selectedDistrict: string;
  schools: SchoolsType[];
  loadingSchools: boolean;
  selectedSchool: string;
  tranferReason: string;
  loadingRequest: boolean;
  upload_percentage: number;
};

class RequestForm extends Component<RequestFormProps, RequestFormState> {
  constructor(props: RequestFormProps) {
    super(props);

    this.state = {
      loading: true,
      error: null,
      selectedFile: null,
      selectReason: false,
      thisReason: null,
      districts: null,
      selectedDistrict: "",
      schools: [],
      loadingSchools: false,
      tranferReason: "",
      selectedSchool: "",
      loadingRequest: false,
      upload_percentage: 0,
    };
  }

  componentDidMount = () => {
    if (this.props.transferReasons === null)
      this.props.FC_GetTransferReasons((status: boolean, msg: string) => {
        this.setState({ loading: status });

        if (!status && !isEmptyOrSpaces(msg))
          this.setState({
            error: { target: "main-error", msg: msg },
          });
        else this.setState({ loading: status });
      });
    else this.setState({ loading: false });

    FC_GetDistricts(
      (status: boolean, data: DistrictTypes[] | null, msg: string) => {
        this.setState({ loading: status });

        if (!status && !isEmptyOrSpaces(msg))
          this.setState({
            error: { target: "main-error", msg: msg },
          });
        else if (!status && data)
          this.setState({ loading: status, districts: data });
      }
    );
  };

  handleSchools = (thisDistrict: string) => {
    FC_GetSchoolByDistrict(
      thisDistrict,
      (status: boolean, data: SchoolsType[] | null, msg: string) => {
        this.setState({ loadingSchools: status });

        if (!status && !isEmptyOrSpaces(msg))
          this.setState({
            error: { target: "main-error", msg: msg },
          });
        else if (!status && data)
          this.setState({ loadingSchools: status, schools: data });
      }
    );
  };

  getSchoolsForm = (): { title: string; value: string }[] => {
    let result: { title: string; value: string }[] = [
      { title: "All Schools", value: "" },
    ];
    // this.props.auth.user.
    this.state.schools.forEach((school) => {
      if (
        this.props.auth.school &&
        this.props.auth.school.school_code !== school.school_code
      )
        result.push({
          title: school.school_name,
          value: school.school_code,
        });
    });
    return result;
  };

  onFileChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({ error: null });
    // this.setState({ selectedFile: event.target.files[0] });

    // check if the file is available
    if (e.target.files === null)
      return this.setState({
        selectedFile: null,
        error: {
          msg: "Please select a valid file",
          target: "approved_doc",
        },
      });

    const thisFile = e.target.files[0];

    // check the file type
    if (thisFile.type !== "application/pdf")
      return this.setState({
        selectedFile: null,
        error: {
          msg: "The system accept pdf only",
          target: "approved_doc",
        },
      });

    // check if the size is valued
    if (thisFile.size <= 0)
      return this.setState({
        selectedFile: null,
        error: {
          msg: "Invalid file, Please choose a real file",
          target: "approved_doc",
        },
      });

    // Add image
    this.setState({
      selectedFile: thisFile,
      error: null,
    });
  };

  // On file upload (click the upload button)
  onSubmitTeacherTransferReq = async () => {
    // Create an object of formData
    const fromSchool = this.props.auth.school;
    const toSchool = this.state.selectedSchool;
    const teacherReason = this.state.tranferReason;

    // checking errors
    if (!fromSchool?.school_code) {
      this.setState({
        error: {
          target: "school-code",
          msg: "Required!",
        },
      });
      return;
    }

    //checking input
    if (this.state.thisReason === null) {
      this.setState({
        error: {
          target: "reason_id",
          msg: "Required!",
        },
      });
      return;
    }

    if (teacherReason === "") {
      this.setState({
        error: {
          target: "comment",
          msg: "Required!",
        },
      });
      return;
    }

    // Document validation
    if (this.state.selectedFile === null)
      return this.setState({
        error: {
          target: "approved_doc",
          msg: "Required!",
        },
        loadingRequest: false,
      });

    let doc_title: string = "";
    await FC_UploadFile(
      this.state.selectedFile,
      (
        status: boolean,
        uploadingPercentage: number,
        data: string | null,
        msg: string
      ) => {
        this.setState({
          loadingRequest: status,
        });
        if (status)
          this.setState({
            upload_percentage: uploadingPercentage,
          });

        if (!status && isEmptyOrSpaces(msg) && data !== null) doc_title = data;
        else if (!status && !isEmptyOrSpaces(msg))
          this.setState({
            error: {
              target: "main-error",
              msg: msg,
            },
            loadingRequest: false,
          });
      }
    );

    console.log("=================doc_title===================");
    console.log({ doc_title });
    console.log("=================doc_title===================");

    // Json Data
    if (!isEmptyOrSpaces(doc_title)) {
      const formData = {
        teacher_supporting_document: doc_title,
        school_from_id: fromSchool.school_code,
        requested_school_id: toSchool,
        teacher_comment: teacherReason,
        teacher_reason_id: this.state.thisReason.transfer_reason_id,
      };
      try {
        setAxiosToken();
        const dataRes = await axios.post<RequestedTransfer>(
          `${APP_URL}/teachertransfers/request`,
          formData
        );

        // console.log("====================================");
        // console.log({ dataRes });
        // console.log("====================================");

        if (dataRes.status === 200) {
          this.props.FC_GetMyTransfers(
            (
              status: boolean,
              data: RequestedTransfer[] | null,
              msg: string
            ) => {
              this.setState({ loadingRequest: status });

              if (!status && data) this.setState({ loadingRequest: status });
              else if (!status && !isEmptyOrSpaces(msg))
                this.setState({
                  error: { target: "main-error", msg: msg },
                });
            }
          );
          this.setState({
            error: {
              target: "main-success",
              msg: "Transfer Request Send Successfully!",
            },
          });
          setTimeout(() => {
            this.setState({
              error: null,
              thisReason: null,
              selectedDistrict: "",
              selectedSchool: "",
              schools: [],
              tranferReason: "",
            });
            this.props.setIsRequestFormOn(false);
          }, 2000);
          return;
        }
      } catch (error) {
        //checking input
        console.error({ error });

        // this.cleanAlert();
        this.setState({
          error: {
            target: "main-error",
            msg: "Something went Wrong, Transfer Request Failed!",
          },
        });
        return;
      }
    }
  };

  render() {
    if (this.state.loading || this.state.districts === null)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    return (
      <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
        {this.state.error && this.state.error.target === "main-success" && (
          <Alert theme="success">{this.state.error.msg}</Alert>
        )}
        {this.state.error && this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}

        <div className="py-2 flex items-center">
          <div className="flex-1">
            <Heading>Staff transfer request form</Heading>
          </div>
          <div className="">
            <Button
              onClick={() => this.props.setIsRequestFormOn(false)}
              theme="default"
            >
              Close
            </Button>
          </div>
        </div>

        <div className="relative p-6 flex-auto">
          <div className="flex gap-6">
            <InputSelect
              data={[
                { title: "All District", value: "" },
                ...this.state.districts.map((el) => ({
                  title: el.district_name,
                  value: el.district_code,
                })),
              ]}
              error=""
              name="select_district"
              onChange={(e) => {
                this.setState({
                  selectedDistrict: e.target.value,
                });
                this.handleSchools(e.target.value);
              }}
              label="District"
              placeholder="Select"
              value={this.state.selectedDistrict}
              size="md"
            />
            <InputSelect
              data={this.getSchoolsForm()}
              error={
                this.state.error && this.state.error.target === "school-code"
                  ? this.state.error.msg
                  : ""
              }
              name="select_school"
              onChange={(e) =>
                this.setState({
                  selectedSchool: e.target.value,
                })
              }
              label="Schools"
              placeholder="Select"
              value={this.state.selectedSchool}
              size="md"
            />
          </div>

          {/* ---------------- */}
          <div className="w-full py-1">
            <div className={`w-full my-2`}>
              <label
                htmlFor="login-form-email"
                className={`text-primary-900 dark:text-primary-100`}
              >
                Transfer reasons
              </label>
              {!this.state.selectReason ? (
                <div className={`rounded relative`}>
                  <div
                    className={`w-full text-lg rounded border-2 p-2 px-4 text-md ${
                      !this.state.thisReason && "text-gray-500"
                    } ${
                      this.props.transferReasons
                        ? "cursor-pointer"
                        : "cursor-wait"
                    } ${
                      this.state.error &&
                      this.state.error.target === "reason_id"
                        ? "border-red-500 dark:text-white bg-red-100 dark:bg-red-900 dark:bg-opacity-30"
                        : "border-primary-800 dark:text-white bg-white dark:bg-primary-900"
                    } px-3`}
                    onClick={() =>
                      this.props.transferReasons &&
                      this.setState({ selectReason: true })
                    }
                  >
                    {this.state.thisReason
                      ? this.state.thisReason.reason_name
                      : "Select transfer reason"}
                  </div>

                  {this.state.error &&
                    this.state.error.target === "reason_id" && (
                      <p className="text-sm text-red-600 dark:text-red-500">
                        {this.state.error.msg}
                      </p>
                    )}
                </div>
              ) : (
                <SelectCustom
                  title="reason_name"
                  id="transfer_reason_id"
                  data={this.props.transferReasons}
                  loading={this.props.transferReasons === null}
                  click={(value: any) =>
                    this.setState({
                      selectReason: false,
                      thisReason: value as TransferReasonsType | null,
                    })
                  }
                  close={() => this.setState({ selectReason: true })}
                />
              )}
            </div>
          </div>
          {/* ---------------------- */}
          <div className="">
            <TextArea
              label="Transfer comment"
              value={this.state.tranferReason}
              name="transfer_comment"
              onChange={(e) =>
                this.setState({
                  tranferReason: e.target.value,
                })
              }
              placeholder="Write reason in summary"
              size="lg"
              error={
                this.state.error && this.state.error.target === "comment"
                  ? this.state.error.msg
                  : ""
              }
            />
            <div className="">
              <InputFile
                disabled={this.state.loadingRequest}
                label="Supporting document"
                name="approved_doc"
                className="p-2"
                onChange={(e) => this.onFileChange(e)}
                error={
                  this.state.error && this.state.error.target === "approved_doc"
                    ? this.state.error.msg
                    : ""
                }
                type={"file"}
                accept={acceptAllEnum.PDF}
              />
            </div>
          </div>
        </div>
        {/*footer*/}
        <div className="flex items-center justify-end py-3 gap-2 border-t border-solid border-blueGray-200 rounded-b">
          <Button
            theme="default"
            onClick={() =>
              this.setState({
                error: null,
                thisReason: null,
                selectedDistrict: "",
                selectedSchool: "",
                schools: [],
                tranferReason: "",
              })
            }
            loading={this.state.loadingRequest}
          >
            Cancel
          </Button>
          <Button
            onClick={() => this.onSubmitTeacherTransferReq()}
            loading={this.state.loadingRequest}
          >
            Save
          </Button>
        </div>
      </DashboardContainer>
    );
  }
}

const mapStateToProps = ({
  transfer,
  auth,
}: StoreState): {
  auth: Auth;
  transferReasons: TransferReasonsType[] | null;
} => {
  return { auth, transferReasons: transfer.transferReasons };
};
export default connect(mapStateToProps, {
  FC_GetTransferReasons,
  FC_GetMyTransfers,
})(RequestForm);
